
import { defineComponent, ref } from 'vue';
import { NDataTable, NButton } from 'naive-ui';
import { getNomadBalances, connectWallet } from '../utils/sdk';
import { tokens, TokenName } from '../config';

export default defineComponent({
  name: 'ui-balances',
  props: {
    address: {
      type: String,
      required: true
    }
  },
  components: {
    NDataTable,
    NButton
  },
  data() {
    return {
      loading: true,
      showBalances: false,
      columns: [
        {
          title: '',
          key: 'asset'
        },
        {
          title: 'Kovan',
          key: '3000'
        },
        {
          title: 'Moonbase Alpha',
          key: '5000'
        }
      ],
      balances: ref([]),
    }
  },
  async mounted() {
    await connectWallet()
    this.getBalances()
  },
  methods: {
    async getBalances() {
      this.loading = true
      let balances: any = []
      for (const token in tokens) {
        const tokenBalances = await getNomadBalances(token as TokenName, this.address)
        balances.push({ asset: tokens[token].symbol, ...tokenBalances })
      }
      this.balances = balances
      this.loading = false
    }
  }
});
